import React, { Component } from 'react';
import PropTypes from 'prop-types';

import lightBaseTheme from 'material-ui/styles/baseThemes/lightBaseTheme';
import darkBaseTheme from 'material-ui/styles/baseThemes/darkBaseTheme';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import getMuiTheme from 'material-ui/styles/getMuiTheme';

import TextField from '@material-ui/core/TextField';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';

import SubmitBtn from './component/SubmitBtn.js';
import LoginBtn from './component/LoginBtn.js';

import './App.css';

import {
  click_func,
  setImgs,
  getImgs
} from './controller/functionset';

const testTimes = 20;

class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      id: 0,        // the same person test times
      username: "", // the user's name
      lpicType: "", // the pic's type on the left hand
      rpicType: "", // the pic's type on the right hand
      chosen : ' ',
      imgsrc1 : require('./module/img/Bedding/15.jpg'),
      randtopic1 : 'Bedding',
      imgsrc2 : require('./module/img/Children/15.jpg'),
      randtopic2 : 'Children',
      msg : '',
      whos: true,
      counter: 0,
    };
  }

  // use fetch post methods
  setDatabase(){
    var id = this.state.id,
        username = this.state.username,
        lpicType = this.state.lpicType,
        rpicType = this.state.rpicType,
        chosen = this.state.chosen;
        
    fetch('http://140.115.213.30:9487/users/database', {
      method: 'post',
      mode: 'cors',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        'id': id,
        'username': username,
        'lpicType': lpicType,
        'rpicType': rpicType,
        'chosen': chosen
      })
    })
      .then((res) => 
        console.log('res')
      )
      .then((req) => 
        console.log('req')
      )
      .catch((error) => 
        console.log(error)
      );
  }

  btonClick = function(side){
    this.setDatabase();
    if(side === 'left' || side === 'right'){
      this.setState({chosen: side});
      var dataset = {
        userName: this.state.username,
        testId: 0,
        picLeft: this.state.randtopic1,
        picRight: this.state.randtopic2,
        chosen: side
      }
      var table = click_func(dataset, testTimes);
      this.setState({
        msg: table.userName + " #" + table.testId + "  [ " + table.picLeft + " / " + table.picRight + " ] chosen: " + table.chosen,
      });
      var id = table.testId,
          username = table.userName,
          lpicType = table.picLeft,
          rpicType = table.picRight,
          chosen = table.chosen;
      this.setState({
        id: id,
        username: username,
        lpicType: lpicType,
        rpicType: rpicType,
        chosen: chosen
      });
      var imgset = getImgs(this.state.imgsrc1, this.state.imgsrc2);
      this.setState({
        imgsrc1 : imgset.imgsrc1,
        imgsrc2 : imgset.imgsrc2,
        randtopic1 : imgset.randtopic1,
        randtopic2 : imgset.randtopic2,
        chosen : side,
      });

      var temp = this.state.counter;
      this.setState({counter: temp + 1});
      if (temp + 1 === testTimes) {
        if (!window.confirm('感謝您參與本次的實驗，我們所獲得的資訊都將用來回饋使用者，不會做非法用途或個人之用。是否願意繼續下一輪的測驗?')){
          this.setState({whos: true});
        }else{
          this.setState({counter: 0});
        }
        
      }
    }
  }

  handleClose = () => {
    this.setState({ open: false });
  };

  // before render
  componentWillMount(){
    var imgsrc = setImgs();
    console.log(imgsrc);
    var imgset = getImgs(this.state.imgsrc1, this.state.imgsrc2);
      this.setState({
        imgsrc1 : imgset.imgsrc1,
        imgsrc2 : imgset.imgsrc2,
        randtopic1 : imgset.randtopic1,
        randtopic2 : imgset.randtopic2,
      });
  }

  // after render
  componentDidMount(){

  }

  // render function
  render() {
    return (
      <div className="App">

        <MuiThemeProvider muiTheme={getMuiTheme(lightBaseTheme)}>
          <Paper elevation={4}>
            <Typography variant="headline" component="h1">
              <br/><h3>Welcome to Customer Analysis System</h3>
            </Typography>
            <Typography component="p">
              This system will make your own intrests table.<br/>
              The only thing you need to do is to choose the picture which you like.<br/>
              <br/>
            </Typography>
          </Paper>
        </MuiThemeProvider>

        {this.state.whos? 
          <div>
            <h3><br/><br/></h3>
            <MuiThemeProvider muiTheme={getMuiTheme(darkBaseTheme)}>
              <form noValidate autoComplete="off">
                <TextField
                  id="name"
                  label="Your name"
                  value={this.state.name}
                  onChange={(text) => this.setState({username: text.target.value})}
                  margin="normal"/>
              </form>
            </MuiThemeProvider>
            <h1><br/></h1>
            <LoginBtn btnonClick={() => this.setState({whos: false})}/>
          </div> : 
          <div className="imgbox">
            <div className="left-img">
              <img className="img1" src={this.state.imgsrc1} alt="left_img" />
              <SubmitBtn btnonClick={() => this.btonClick('left')} isopen={true} message={this.state.msg} />
            </div>
            <div className="right-img">
              <img className="img2" src={this.state.imgsrc2} alt="right_img" />
              <SubmitBtn btnonClick={() => this.btonClick('right')} isopen={true} message={this.state.msg} />
            </div>
          </div>
        }
        <h3><br/><br/><br/><br/></h3>
        <MuiThemeProvider muiTheme={getMuiTheme(darkBaseTheme)}>
          <Paper elevation={1}>
            <Typography variant="headline" component="h4">
              <br/>Thanks for useing the Customer Analysis System<br/>
            </Typography>
            <Typography component="p">
              &copy; 2018 KW<br/><br/>
              We are useing ReactJs and it is awesome!<br/>
              Follow my <a className="link_github" href="https://github.com/">github</a> to get more info.<br/>
              <br/>
            </Typography>
          </Paper>
        </MuiThemeProvider>
      </div>
    );
  }
}

App.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default App;


